.contact {
  display: grid;
  grid-column: 1/-1;

  @include onDesktop {
    grid-column: 1/6;
    grid-template-columns: repeat(12, 1fr);
  }

}

.contacts {
  color: #7e7e83;
  font-size: 14px;
  font-family: $text-font-family;
  font-weight: 400;
  row-gap: 16px;

  @include pageGrid;

  &__form {
    margin: 0;
    justify-content: start;
    font-family: $text-font-family;
    margin-bottom: 16px;
    grid-column: 1/-1;

    &--message {
      width: 100%;
      height: auto;
      margin: 0;
      margin-bottom: 32px;
      border-radius: 8px;
      background: #f8f8fa;
      border: none;
      align-items: flex-start;
      resize: none;
    }

    @include onTablet {
      grid-column: 1/-1;
    }
  }

  &__button {
    grid-column: 1/-1;
  }

  &__information {
    grid-column: 1/2;
    margin-top: 48px;

    @include onTablet {
      margin-top: 80px;
    }

    @include onDesktop {
      margin-top: 0;
    }

    @include onDesktop {
      grid-column: 7 / -1;
    }
  }

  &__link {
    text-decoration: none;
    color: #191919;

    &:hover {
      color: #207c7c;
    }
  }

  &__text {
    color: #9393a3;
    margin: 0;

    &--phone {
      @include onDesktop {
        margin-top: 0;
      }
    }
  }
}

input {
  width: 100%;
  height: 48px;
  // padding: 14px 6px;
  border-radius: 8px;
  background: #f8f8fa;
  border: none;
}

input:hover {
  border-color: #d5d7de;
}

input:focus {
  border-color: $main-text-color;
  font-family: $main-text-font-family;
  outline: none;
}

input::placeholder {
  color: #7e7e83;
  font-family: "Space Mono", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 140%;
}
