.section-title {
  margin: 0;
  margin-bottom: 48px;
  font-family: $main-text-font-family;

  font-weight: 700;
  font-size: 32px;
  line-height: 100%;
  text-align: left;

  @include onTablet {
    margin-bottom: 56px;
    font-size: 40px;
  }

  @include onDesktop {
    margin-bottom: 56px;
    font-size: 48px;
  }
}
